import React, { useState } from "react";
import AboutMe from "./AboutMe";
import Analytics from "./Analytics";
import { ThemeProvider } from "./Common/navbarTheme";
import Contact from "./Contact";
import Introduction from "./Introduction";
import Navbar from "./Navbar";
import Projects from "./Projects";
import Skills from "./Skills";

function Home(props) {
  //THEME DEFAULT STATE AND HANDLER
  const [theme, setTheme] = useState("#131416");
  const setThemeValue = (val) => {
    setTheme(val);
  };

  const state = {
    theme: theme,
    setThemeValue,
  };
  return (
    <ThemeProvider value={state}>
      <Navbar />
      <Introduction />
      <AboutMe />
      <Analytics />
      <Projects />
      <Skills />
      <Contact />
      {/* <Footer /> */}
    </ThemeProvider>
  );
}

export default Home;
