import React, { useEffect, useState } from "react";
import { InView } from "react-intersection-observer";
import { ThemeConsumer } from "./Common/navbarTheme";
import Footer from "./Footer";

function Contact(props) {
  const [greet, setGreet] = useState("Good Morning");

  useEffect(() => {
    const hour = new Date().getHours();
    const welcomeTypes = [
      "Good morning",
      "Good afternoon",
      "Good evening",
      "Good night",
    ];
    let welcomeText = "";

    if (hour >= 4 && hour < 12) welcomeText = welcomeTypes[0];
    else if (hour >= 12 && hour < 18) welcomeText = welcomeTypes[1];
    else if (hour >= 18 && hour <= 20) welcomeText = welcomeTypes[2];
    else if (hour > 20 || hour < 4) welcomeText = welcomeTypes[3];
    else welcomeText = "Have a great day!";
    setGreet(welcomeText);
  }, []);
  //THEME CONSUMER
  return (
    <ThemeConsumer>
      {({ setThemeValue }) => (
        <InView
          as="div"
          threshold={0.86}
          onChange={(inView, entry) => {
            if (inView) setThemeValue("#0b2838");
            else setThemeValue("#131416");
          }}
        >
          <div className="contact">
            <svg
              preserveAspectRatio="none"
              viewBox="0 0 100 102"
              height="75"
              width="100vw"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              className="svgcolor-light"
            >
              <path
                d="M0 0 L48 100 L100 0 Z"
                fill="#131416"
                stroke="#131416"
              ></path>
            </svg>
            <div className="contact-container" id="contact">
              <div className="section-title">
                <div className="square"></div>
                Let's build something great together
              </div>
              <div className="contact-container-content">
                {/* <div className="thank-you"></div> */}
                <form action="">
                  <input type="text" placeholder="Name" name="name" id="name" />
                  <input
                    type="email"
                    placeholder="Email ID"
                    name="email"
                    id="email"
                  />
                  <textarea
                    name="message"
                    id="message"
                    placeholder="Add Message"
                    cols="30"
                    rows="7"
                  ></textarea>
                  <input type="submit" value="Submit" className="submit" />
                </form>
              </div>
              <div className="gratitude">Thanks for visiting. {greet}!</div>
            </div>
            <Footer />
          </div>
        </InView>
      )}
    </ThemeConsumer>
  );
}

export default Contact;
